@import '../index.scss';

.whiteButtonCreate {
  position: absolute;
  width: 99px;
  height: 44px;
  border-radius: 22px;
  top: 282px;
  left: 251px;
  right: 30px;
  bottom: 32px;
  border-color: $purple;
  background: #FFFFFF;
}

.whiteButtonEnter {
  position: absolute;
  width: 99px;
  height: 44px;
  border-radius: 22px;
  left: 251px;
  top: 572px;
  right: 30px;
  bottom: 32px;
  border-color: $purple;
  background: #FFFFFF;
}

.whiteButtonText{
  left: 18px;
  right: 19px;
  top: 14px;
  bottom: 15px;
  position: absolute;
  width: 62px;
  height: 15px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  text-transform: uppercase;
  color: $purple;
}

@mixin buttonGradient($top, $left) {
  position: static;
  width: 320px;
  height: 44px;
  background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
  border-radius: 22px;
  margin: 20px 0px 30px 0px;
  bottom: 106px;
  right: 30px;
}

.buttonLogin {
  @include buttonGradient(208px, 30px);
}

.buttonToPayment {
  @include buttonGradient(498px, 30px)
}

.insideText {
  position: absolute;
  width: 280px;
  height: 15px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  left: 20px;
  right: 20px;
  top: 14px;
  bottom: 15px;
}

.arrow {
  position: absolute;
  width: 44px;
  height: 44px;
  font-family: Material Icons;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  left: 276px;
}